@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
*{margin:0;padding:0;border:0;box-sizing:border-box}html,body,#root{margin:0;height:100%;width:100%}*{font-family:'Lato', sans-serif}.app{display:flex;flex-direction:column;align-items:center;color:#333;height:100%;width:100%;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.app a{outline:none;text-decoration:none}.app a:link,.app a:visited{color:#4575b4}.app a:active{color:#d73027}.app a:focus,.app a:hover{color:#d73027;border-bottom:1px dotted}.app h1{font-size:15px;font-weight:700;line-height:1.5em;margin-top:15px;margin-bottom:5px}.app__footer{font-size:12px;text-align:center;margin-top:10px}.country{stroke:#fff;stroke-width:0.4}.country:hover{fill:#fc9272}.legend{display:flex;flex-direction:column;align-items:center;margin:5px 0}.legend__range{font-size:13px;text-align:center}.legend__axis path{display:none}.legend__axis .tick line{color:#fff}.legend__axis text{font-size:12px}.loading{height:100%;width:100%;display:flex}.loading__wheel{height:100px;width:100px;margin:auto;border:16px solid #fff;border-top-color:#4575b4;border-bottom-color:#d73027;border-radius:50%;-webkit-animation:spin 3s linear infinite;animation:spin 3s linear infinite}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.tooltip{display:flex;flex-direction:column;background:rgba(255,255,255,0.85);position:absolute;top:-12px;left:0;padding:0.6em;border:1px solid #f8f8f8;pointer-events:none;font-size:12px}.tooltip__title{font-size:14px;font-weight:700;margin-bottom:0.2em}.worldMap{margin:5px 0}.slider{margin:5px 0}.slider__label{text-align:center}.slider__label__value{font-size:13px}.slider input{margin-top:15px;width:400px;-webkit-appearance:none}.slider input:focus{outline:none}.slider input::-webkit-slider-runnable-track{width:100%;height:10px;cursor:pointer;box-shadow:none;background:#d3d3d3;border-radius:0px}.slider input::-moz-range-track{width:100%;height:10px;cursor:pointer;box-shadow:none;background:#d3d3d3;border-radius:0px}.slider input::-webkit-slider-thumb{box-shadow:none;border:2px solid #fc9272;height:32px;width:22px;border-radius:22px;background:#fff;cursor:pointer;-webkit-appearance:none;margin-top:-12px}.slider input::-webkit-slider-thumb:hover{background:#fc9272}.slider input::-moz-range-thumb{box-shadow:none;border:2px solid #fc9272;height:32px;width:22px;border-radius:22px;background:#fff;cursor:pointer;-webkit-appearance:none;margin-top:-12px}.slider input::-moz-range-thumb:hover{background:#fc9272}.slider input::-moz-focus-outer{border:0}.slider__minmax{margin-top:12px;width:400px;display:flex;justify-content:space-between;font-size:12px}

